:root {
  --font-scale: 100%;
}

html {
  font-size: calc(16px * (var(--font-scale, 100) / 100));
}

body {
  line-height: 1.5;
  margin: 0;
  height: 100%;
}

button, input, select, textarea {
  font-size: inherit;
}

@media (max-width: 768px) {
  :root {
    --font-scale: calc(100% * 0.95);
  }
}

.js-script-code {
  text-decoration: underline red 1px;
}

.js-comment {
  // color:#808080;
  font-size: 1rem;
}

.js-script-background {
  padding: 1rem;
  background-color: ghostwhite;
  border: 1px solid #ccc;
}

.anchor-animation-enter {
  animation-duration: 3s;
  animation-name: slidein;
}

@keyframes slidein {
  from {
    margin-left: 100%;
    width: 100%;
  }

  to {
    margin-left: 0%;
    width: 100%;
  }
}
* {
  scroll-margin-top: 6rem;
}

html, body{ margin:0; height:100%; }

.autoImage{
  // width: 100%;
  max-width: 100%;
  // height: auto;
  // object-fit: contain;
}

.doc-variable-text{
  font-weight: bold;
  background-color: #ccc;
  padding: 0.2rem;
  border-radius: 0.2rem;
}

body [data-nextjs-toast] {
  display: none !important;
}

/* Disable Next.js error overlay */
.next-error-hide {
  display: none !important;
}

/* Ensure body is always scrollable */
body {
  overflow: auto !important;
  padding-right: 0 !important;
}